@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,700&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Noto+Serif:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Syncopate&display=swap");
/* @import url('https://fonts.googleapis.com/css2?family=Noto+Serif:wght@700&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Ubuntu+Condensed&display=swap');

.quote {
  font-size: 1.5rem;
  font-family: "Merriweather", serif;
  background-color: #ffc107c7;
  padding: 0.5rem;
}
.my_bg {
  background-size: cover;
  background-attachment: fixed;
  /* background-color: crimson; */
}
.my_bg2{
  background-color: rgba(0, 0, 0, 0.116);
}
.my_styel {
  font-size: 2rem;
  font-weight: bold;
  font-family: "Noto Serif", serif;
  /* font-family: 'Ubuntu Condensed', sans-serif; */
  /* font-family: "Merriweather", serif; */

  color: rgb(199, 194, 194);
  padding-top: 2rem;
  padding-bottom: 1.5rem;
  /* margin: 2rem; */

}
.stream {
  font-size: 2rem;
  padding: 0.1rem;
  font-weight: normal;
  line-height: 2.3rem;
  font-family: "Noto Serif", serif;
}
/* 1rem = 10px , 15px */
.img {
  height: 17rem;
  width: 17rem;
  border-radius: 15rem;
  margin-top: 1.8rem;
  margin-bottom: 1rem;

  padding: 1rem;
  /* border: 0.4rem solid azure; */
  box-shadow: -3px 1px 66px 26px azure;
}
.icon {
  font-size: 5rem;
  color: rgb(184, 184, 184);
  margin-top: 5rem;
  margin-bottom: 8rem;

  /* margin-bottom: 10rem; */
}
.my_log {
  
  font-size: 4rem;
  color: rgba(184, 184, 184, 0.986);
  /* color: black; */
  margin-bottom: 5rem;
  margin-top: 8rem;
  /* width: 20rem; */
  padding-left: 2.5rem;
  padding-right: 2.5rem;

  /* margin: 8rem; */
  border-radius: 7rem;
  /* margin: auto; */
  font-family: "Syncopate", sans-serif;
  background-color:rgba(255, 255, 255, 0.123);
  /* text-shadow: 2px 2px 4px #ffc107c7; */
  text-shadow: 2px -1px 31px #ffc107c7;
}
