@import url('https://fonts.googleapis.com/css2?family=Noto+Serif:wght@700&display=swap');

.my_bg{
    background-size: cover;
    background-attachment: fixed;
    /* pa-bottom: 10rem; */

}
.heading{
    font-size: 2rem;
    font-weight: bolder;
    color: rgb(224, 224, 224);
    font-family: 'Noto Serif', serif;
    /* background-color: #172e41; */
    background-color: #00000038;

    
    padding: 0.5rem;

}
.my_img{
    width:15rem;

}
.chat{
    font-size: 1rem;
    width: 9rem;
    height: 9rem;
    /* font-weight: bolder; */
    color: white;
    font-family: 'Noto Serif', serif;
    border : .3rem solid #d5ffff;
    border-radius: 10rem;
    padding: 4rem;
    background-color: #12032180;
    margin: 3rem;
    /* box-shadow: 14px -4px 203px 30px #d5ffff; */
    box-shadow: 0px 0px 54px 0px #d5ffff;

    
}

.chat_p{
    margin-top: 8rem;
    animation-name: gole;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    /* transform: rotate(-45deg); */
}
@keyframes gole {

    from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}

}
.e_p{
    margin-top: 8rem;
    animation-name: gole1;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}
@keyframes gole1 {

    from {transform: rotate(360deg);}
  to {transform: rotate(0deg);}

}
.e_t{
    margin-top: 8rem;
    animation-name: gole;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}
.p_t{
    text-decoration: none;
    color: rgba(255, 255, 255, 0.801);
    font-size: 1.5rem;
    margin: 0px;
    padding: 0px;
}
.size{
    font-size: 3rem;
    color: rgb(118, 139, 255);
}
.bg_black{
    width: 100%;
    background-color: rgba(0, 0, 0, 0.493);
    padding-bottom:8rem ;

}